<template>
  <uv-transition mode="fade" :customStyle="backTopStyle" :show="show">
    <slot>
      <view class="uv-back-top" :style="[contentStyle]" @click="backToTop">
        <uv-icon :name="icon" :custom-style="iconStyle"></uv-icon>
        <text v-if="text" class="uv-back-top__text">{{ text }}</text>
      </view>
    </slot>
  </uv-transition>
</template>

<script>
export default {
  name: 'paBackToTop',
  props: {
    // 返回顶部的形状，circle-圆形，square-方形
    mode: {
      type: String,
      default: 'circle',
    },
    // 自定义图标
    icon: {
      type: String,
      default: 'arrow-upward',
    },
    // 提示文字
    text: {
      type: String,
      default: '',
    },
    // 返回顶部滚动时间
    duration: {
      type: [String, Number],
      default: 200,
    },
    // 滚动距离
    scrollTop: {
      type: [String, Number],
      default: 0,
    },
    // 距离顶部多少距离显示，单位px
    top: {
      type: [String, Number],
      default: 400,
    },
    // 返回顶部按钮到底部的距离，单位px
    bottom: {
      type: [String, Number],
      default: 150,
    },
    // 返回顶部按钮到右边的距离，单位px
    right: {
      type: [String, Number],
      default: 20,
    },
    // 层级
    zIndex: {
      type: [String, Number],
      default: 9,
    },
    // 图标的样式，对象形式
    iconStyle: {
      type: Object,
      default: () => ({
        color: '#909399',
        fontSize: '19px',
      }),
    },
  },
  computed: {
    backTopStyle() {
      const style = {
        bottom: this.bottom + 'px',
        right: this.right + 'px',
        width: '40px',
        height: '40px',
        position: 'fixed',
        zIndex: 10,
      }
      return style
    },
    show() {
      return this.scrollTop > this.top
    },
    contentStyle() {
      const style = {}
      let radius = 0
      // 是否圆形
      if (this.mode === 'circle') {
        radius = '100px'
      } else {
        radius = '4px'
      }
      return {
        borderRadius: radius,
      }
    },
  },
  methods: {
    backToTop() {
      uni.pageScrollTo({
        scrollTop: 0,
        duration: this.duration,
      })
      this.$emit('click')
    },
  },
}
</script>

<style scoped lang="scss">
.uv-back-top {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  height: 100%;
  justify-content: center;
  background-color: #E1E1E1;
  &__tips {
    font-size: 12px;
    transform: scale(0.8);
  }
  &__text {
    font-size: 12px;
    transform: scale(0.8);
  }
}
</style>
